import React from 'react'
import { Link } from 'gatsby'
import { IconBaseProps } from 'react-icons'

export type IconProps = {
  element: React.FC
  to?: string
  href?: string
} & IconBaseProps

export const Icon: React.FC<IconProps> = ({ to, href, element, ...rest }) => {
  const Element = element

  return to ? (
    <Link to={to}>
      <Element {...rest} />
    </Link>
  ) : href ? (
    <a href={href}>
      <Element {...rest} />
    </a>
  ) : (
    <Element {...rest} />
  )
}

export default Icon
